<template>
  <div class="activityForm">
    <h3>Rapport d'activité</h3>
    <form>
      <vs-input disabled label="Votre pseudo sur Habbo" v-model="this.$parent.accountData.user.name"/>
      <vs-input-number label="Numéro du rapport" type="number" v-model="form.id" required/>
      <vs-input-number label="Nombre de personnes" type="number" v-model="form.people" required/>
      <vs-input-number label="Nombre de gradés" type="number" v-model="form.officers" required/>
      <vs-input label="Commentaires" placeholder="Ex: R.A.S" v-model="form.comment"/>
      <vs-input :danger="invalidScreen" danger-text="Veuillez entrer un lien valide" label="Lien vers l'image de votre screen" type="url" placeholder="Ex: http://abc.xyz/image.png" v-model="form.screen" required/><br/>
      <vs-button color="success" type="relief" @click="sendMsg()">Envoyer</vs-button>
    </form>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: 'ActivityForm',
  components: {
  },
  data: function() {
    return {
      form: {
        message:"",
        date:"",
        time:"",
        id:"0",
        people:"0",
        officers: "0",
        screen: "",
      },
      invalidScreen: false,
      stopState: false,
    }
  },
  methods: {
    sendMsg: function(){

      if(!this.validURL(this.form.screen)) { //Checking if it is a valid URL
        this.invalidScreen = true
        return
      }else{
        this.invalidScreen = false
      }
      // Calculating date and time
      this.form.time = this.getTime
      this.form.date = this.getDate
      // Setting the message up
      this.form.message = "[center]:badgepolice [/center]" + '\n'
      + "[center][size=18]:médaille2: [/size][size=18][b]Rapport d'activité n°[color=#A01B1E]" + this.form.id + "[/color][/b] :médaille2:[/size][/center]" + '\n'
      + '\n'
      + "[center][size=16][b]:Vers la droite Date : [color=#A01B1E]" + this.form.date + "[/color][/b][/size][/center]" + '\n'
      + "[center][b][size=16]:Vers la droite Lieu : Police Nationale[/size][/b][/center]" + '\n'
      + "[center][b][size=16]:Vers la droite Heure: [color=#A01B1E]" + this.form.time + "[/color][/size][/b][/center]" + '\n'
      + "[center][b][size=16][b][size=16]:Vers la droite [/size][/b]Nombre de personnes : [color=#A01B1E]" + this.form.people + "[/color][/size][/b][/center]" + '\n'
      + "[center][b][size=16][b][size=16]:Vers la droite [/size][/b]Nombre de gradés : [color=#A01B1E]" + this.form.officers + "[/color][/size][/b][/center]"+ '\n'
      + "[center][b][size=16]:exclamation: Commentaires : [color=#A01B1E]" + this.form.comment + "[/color][/size][/b][/center]" + '\n'
      + "[center][b][size=16]:idée: Capture d'écran :" + '\n'
      + "[/size][/b][spoiler='Screen'][img]" + this.form.screen + "[/img][/spoiler][/center]" + '\n'
      + '\n'
      + "[center]:badgepolice [/center]"

      // Copying the BBCode
      this.$copyText(this.form.message)
      this.$vs.notify({title:'Succès !',text:'Message copié !',color:'success',position:'top-center'})
    },
    // Number beautifier
    addZero: function(i) {
      if (i < 10) {
        i = "0" + i;
      }
      return i
    },
    //URL check
    validURL: function(str) {
      var pattern = new RegExp('^(https?:\\/\\/)?'+ // protocol
        '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|'+ // domain name
        '((\\d{1,3}\\.){3}\\d{1,3}))'+ // OR ip (v4) address
        '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*'+ // port and path
        '(\\?[;&a-z\\d%_.~+=-]*)?'+ // query string
        '(\\#[-a-z\\d_]*)?$','i'); // fragment locator
      return !!pattern.test(str);
    },
  },
  computed: {
    // Get the time like 12:00
    getTime: function() {
      var d = new Date();
      var h = this.addZero(d.getHours());
      var m = this.addZero(d.getMinutes());
      d = h + ":" + m
      return d
    },
    getDate: function() {
      var today = new Date();
      var dd = String(today.getDate()).padStart(2, '0');
      var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
      var yyyy = today.getFullYear();
      today = dd + '/' + mm + '/' + yyyy
      return today
    },
  },
  beforeCreate: function() {
    this.$parent.checkConnected()
  },
}
</script>
<style lang="stylus">
form
  margin-right auto
  margin-left auto
  display inline-block
.activityForm .vs-input, .vs-input-number
  margin-top 10px
  width 500px
  text-align left
</style>
